
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html, body {
    background-color: #1b1b32;
    
}

.App {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding-top: 15px;
    font-family: Lato, sans-serif;
    flex-direction: column;
}

.freecodecamp-logo-contenedor {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.freecodecamp-logo {
    height: 100px;
    padding: 30px;
}

.contenedor-principal {
    height: 100%;
    min-width: 600px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}